import { Component } from "@angular/core";
import { IonicPage, NavController, LoadingController, AlertController, ToastController, NavParams } from "ionic-angular";
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-checkout',
  segment: 'checkout'
})

@Component({
  selector: 'page-checkout',
  templateUrl: 'checkout.html'
})
export class CheckoutPage {
  public paymethods: string = 'creditcard';
  property: any;
  price: any = '200';
  walletBalance: any;
  amount: number;
  value:any;
  wallet: boolean = false;
  textfield: boolean = false;
  currencyName: any;
  tokenprice: any;
  Pay: boolean = false;
  dep: boolean = false;
  next1: boolean = true;
  totalamount: any;
  show: boolean = false;
  token: number;
  disable: boolean = false;
  leftright: any;
  amount1: any;
  showdeposit: boolean = false;
  hidebuy: boolean = false
  purchase: boolean = false;
  sharevalue: any;
  check: boolean;
  counter: number = 0;
  time: any;
  Name: any;
  TokensAvailable:any;
  CountryId:any;
  DECIMAL_SEPARATOR=".";
  GROUP_SEPARATOR=",";
  budget=0;


  constructor(public nav: NavController, public translate: TranslateService, public user: UserProvider, public forgotCtrl: AlertController, public navparams: NavParams, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    // set sample data
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.property = this.navparams.get('property');
    this.CountryId = this.navparams.get('CountryId');
    this.Name = this.property.Name;
    this.TokensAvailable = this.property.Fractions - this.property.FractionsSold;
    this.token = JSON.parse(this.property.PriceperFraction);
    console.log(this.Name, this.token, this.CountryId);





  }

  format(valString) {
    if (!valString) {
        return '';
    }
    let val = valString.toString();
    const parts = this.unFormat(val).split(this.DECIMAL_SEPARATOR);
    return parts[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, this.GROUP_SEPARATOR) + (!parts[1] ? '' : this.DECIMAL_SEPARATOR + parts[1]);
};

unFormat(val) {
    if (!val) {
        return '';
    }
    val = val.replace(/^0+/, '');

    if (this.GROUP_SEPARATOR === ',') {
        return val.replace(/,/g, '');
    } else {
        return val.replace(/\./g, '');
    }
};


  ngOnInit() {
    this.updateBalance();
    this.user.jurisdictionlistBank().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        this.check = this.user.checkDataEmpty(data2.data);
        console.log(this.check);
        if (this.check) {
          this.nav.setRoot('page-general_error');

        } else {

          console.log(data2.data);
        }


      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.nav.setRoot('page-general_error');
        }, 1000);
      },

    )


  }


  updateBalance() {
    this.user.showBalance().subscribe(
      data => {
        console.log(data);

        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        console.log(data2);

        this.check = this.user.checkDataEmpty(data2.data);
        console.log(this.check);
        if (this.check) {
          this.nav.setRoot('page-general_error');

        } else {

          if (data2.data.length === 0) {
            if (this.counter == 0) {
              this.time = setTimeout(() => {
                this.updateBalance();

                this.counter = this.counter + 1;
              }, 10000);

              this.walletBalance = '0.00';
              this.currencyName = "R";

            } else if (this.counter == 1) {
              clearTimeout(this.time);
              this.time = setTimeout(() => {
                this.updateBalance();

                this.counter = this.counter + 1;
              }, 5000);


              this.translate.get('checkout-page').subscribe((translated: string) => {
                var Unavailable_text = this.translate.instant('checkout-page.Unavailable_text');

                this.walletBalance = Unavailable_text;
                this.currencyName = "R";

              })




            }
            else {

            }


          } else {
            this.walletBalance = data2.data[0].Available;
            if (parseInt(this.walletBalance) === 0) {
              this.hidebuy = false;
              this.showdeposit = true;

            } else {
              this.hidebuy = true;
              this.showdeposit = false;
            }
            this.currencyName = data2.data[0].LocalCurrencySymbol;
            this.leftright = data2.data[0].LocalCurrencySymbolPos;
            console.log(this.leftright + "ssssss");

          }


        }

      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.nav.setRoot('page-general_error');
        }, 1000);
      },
    )
    console.log(this.property);

  }
  // go to summary transaction summary page
  next() {

    this.walletBalance = parseInt(this.walletBalance);
    this.totalamount = parseInt(this.totalamount);
    console.log(this.walletBalance, this.totalamount);

    if (this.amount == null || this.amount == 0) {

      this.translate.get('checkout-page').subscribe((translated: string) => {
        var toast1 = this.translate.instant('checkout-page.toast1');

        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'
        });

        toast.onDidDismiss(() => {
          console.log('Dismissed toast');

        });

        toast.present();

      })
    }
    else {
      if (this.walletBalance > this.totalamount) {
        console.log(this.amount);
        this.nav.push('page-make-deposit', { "legalid": this.property.LegalId, "Totalamount": this.totalamount, "shares": this.amount, 'countryId': JSON.parse(this.property.CountryId) });

      } else {

        console.log(this.amount);


      }

    }
  }

  goDespoit() {
    this.nav.push('page-my-wallet');
  }


  codeInput(event) {

    this.amount = this.unFormat(this.amount);

    this.sharevalue = this.amount *  this.property.PriceperFraction;
    
    
    if (this.sharevalue > parseInt(this.walletBalance)) {
      // alert(this.sharevalue);

      this.purchase = true;
      this.hidebuy = false;
      this.showdeposit = false;

    }
    else if (this.sharevalue < parseInt(this.walletBalance)) {
      // alert(this.sharevalue + "else");

      this.purchase = false;
      this.hidebuy = true;
      this.showdeposit = false;

    }

  }







  max() {


    this.amount = Math.round(this.walletBalance / this.property.PriceperFraction);
    console.log(this.amount);
    this.sharevalue = this.amount * this.property.PriceperFraction;


    console.log(this.sharevalue);
    if (this.sharevalue > parseInt(this.walletBalance)) {

      this.purchase = true;
      this.hidebuy = false;
      this.showdeposit = false;

    }
    else if (this.sharevalue < parseInt(this.walletBalance)) {

      this.purchase = false;
      this.hidebuy = true;
      this.showdeposit = false;

    }

  }

  complete() {

    this.nav.push('page-make-deposit', { "complete": 1, "purchase": this.purchase, "legalid": this.property.LegalId, "Totalamount": this.sharevalue, "shares": this.amount, 'countryId': this.CountryId });


  }

  buy() {


    this.nav.push('page-make-deposit', { "legalid": this.property.LegalId, "Totalamount": this.sharevalue, "shares": this.amount, 'countryId': this.CountryId });




  }
  deposit() {


    var amount2 = this.walletBalance - this.amount;

    var realamount = this.sharevalue - this.walletBalance;


    this.nav.push('page-add-amount', { "data": { 'LocalCurrencySymbol': this.currencyName, 'amount': realamount, "legalid": this.property.LegalId, "Totalamount": this.sharevalue, "shares": this.amount, 'Name': this.Name, 'specialid': 1, 'countryId': this.property.CountryId } });

  }


  deposit1() {

    var timezero = {
      'LocalCurrencySymbol': this.currencyName
    };

    this.nav.push('page-add-amount', { "data1": timezero });

  }
}



